.App {
  text-align: center;
  margin-top: 20vh;
  font-family: 'Courier New', Courier, monospace;
}

.cursor {
  display: inline-block;
  width: 1px;
  background-color: black;
  animation: blink 0.7s infinite;
}

@keyframes blink {
  0%, 50% {
    opacity: 1;
  }
  51%, 100% {
    opacity: 0;
  }
}
